.main-container {
  width: 100%;
  height: auto;
  background-color: #00aeef;
  color: #fff;
  /* span {
        text-align: center;
        font-weight: bold;
        background-color: #028bbd;
        padding: 15px;
        border-radius: 150px;
    } */ }
  .main-container h1 {
    width: 100%;
    font-size: 35px;
    text-align: center; }
  .main-container .main-banner {
    padding: 200px 0px;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    justify-items: center; }
    .main-container .main-banner button {
      margin: 10px 0px 20px 0px;
      float: left;
      padding: 10px 50px; }

.about-container {
  background: url("../../../images/stones.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  padding: 200px 0;
  font-family: 'Roboto', sans-serif;
  letter-spacing: .2px;
  text-align: center;
  position: relative;
  color: #fff; }
  .about-container .about-container-inside .row {
    max-width: 600px;
    margin-left: auto;
    margin-right: auto; }
  .about-container .about-container-inside .social-icons {
    margin: 40px 0px 20px 0px; }
    .about-container .about-container-inside .social-icons a {
      color: #fff; }
  .about-container:before {
    opacity: 0.75;
    content: '';
    display: block;
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background: #282623; }
  .about-container h1 {
    text-shadow: 2px 1px 2px #3b3b3b;
    font-size: 30px; }
  .about-container ul {
    display: inline-block;
    padding: 0;
    width: 100%;
    height: 100%; }
    .about-container ul li {
      list-style: none;
      float: left;
      width: 100%;
      font-size: 15px;
      font-weight: 100;
      text-shadow: 2px 1px 2px #3b3b3b; }
  .about-container p {
    font-size: 15px;
    font-weight: 100;
    text-shadow: 2px 1px 2px #3b3b3b; }
  .about-container a {
    font-size: 30px;
    margin: 0px 40px; }
  .about-container i {
    cursor: pointer;
    font-size: 30px;
    transition: .2s;
    position: relative; }
    .about-container i:hover {
      color: #282623; }
    .about-container i a {
      width: 100%;
      height: 100%; }

.using-container {
  padding: 125px 0px 200px;
  text-align: center;
  counter-reset: heading; }
  .using-container p {
    font-size: 16px; }
  .using-container h2 {
    color: #3b3b3b; }
  .using-container .using-item {
    margin: 50px 0px 0px;
    font-size: 17px;
    color: #3b3b3b;
    position: relative; }
    .using-container .using-item div {
      padding: 15px 0px 30px 0px;
      color: #00aeef;
      font-size: 50px; }
    .using-container .using-item:before {
      display: flex;
      justify-content: center;
      flex-direction: column;
      counter-increment: heading;
      content: counter(heading);
      position: absolute;
      width: 35px;
      height: 35px;
      border-radius: 999px;
      box-shadow: 0px 0px 3px 0px grey;
      color: #3b3b3b;
      font-size: 20px;
      left: 20%; }

.main-features-block {
  padding: 50px 0px 100px; }
  .main-features-block .main-features {
    margin: 50px 0px 25px 0px;
    color: #494d49;
    text-align: center; }
    .main-features-block .main-features .divider {
      display: block; }
  .main-features-block .main-features-group {
    display: flex;
    justify-content: center; }
    .main-features-block .main-features-group .features-item {
      width: 100%;
      margin: 0px 0px 50px 0px; }
      .main-features-block .main-features-group .features-item h3 {
        text-align: center;
        font-size: 20px;
        margin: 10px 0px;
        color: #494d49; }
      .main-features-block .main-features-group .features-item .icon-circle {
        display: flex;
        justify-content: center;
        margin: 10px 0px 20px 0px; }
        .main-features-block .main-features-group .features-item .icon-circle span {
          display: flex;
          justify-content: center;
          flex-direction: column;
          border: 2px solid #3b3b3b;
          border-radius: 150px;
          width: 120px;
          height: 120px; }
          .main-features-block .main-features-group .features-item .icon-circle span i {
            color: #00aeef;
            width: 100%;
            font-size: 50px;
            text-align: center; }
      .main-features-block .main-features-group .features-item p {
        text-align: center; }
  .main-features-block .divider {
    display: none; }

.feedback-container {
  background: url("../../../images/book.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  padding: 100px 0;
  position: relative;
  color: #fff;
  text-align: center; }
  .feedback-container p {
    color: #fff; }
  .feedback-container:before {
    opacity: 0.75;
    content: '';
    display: block;
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background: #282623; }

@media (max-width: 991px) {
  .main-container h1 {
    font-size: 25px; }
  .main-container .main-banner {
    padding: 100px 0px; }
  .main-features h1 {
    text-align: center; }
  .main-features-item {
    margin: 15px 0px; }
  .divider {
    display: block !important; } }

@media (max-width: 576px) {
  .main-container h1 {
    font-size: 15px; }
  .main-container span {
    font-size: 13px;
    padding: 10px; }
  .main-features h1 {
    text-align: center;
    font-size: 20px; }
  .about-container {
    padding: 40px 0 20px 0px; }
    .about-container .about-container-inside .social-icons {
      margin: 30px 0px 30px 0px; }
    .about-container .about-container-inside a {
      font-size: 20px;
      margin: 0px 20px; }
    .about-container .about-container-inside i {
      font-size: 30px; }
  .feedback-container .label-30 {
    font-size: 20px; }
  .feedback-container .feedback-text-col {
    margin: 20px 0px; } }
